import React, { useState, useEffect } from 'react';
import { Form, Button, FormControl, Alert } from 'react-bootstrap';
import axios from 'axios';
import { Typeahead } from 'react-bootstrap-typeahead';

const SearchForm = () => {
  const [departments, setDepartments] = useState([]);
  const [selectedDepartment, setSelectedDepartment] = useState([]);
  const [query, setQuery] = useState('');
  const [result, setResult] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    // const fetchDepartments = async () => {
    //   try {
    //     const response = await axios.get('/api/ksmc');
    //     setDepartments(response.data);
    //   } catch (err) {
    //     setDepartments([
    //       '*',
    //       '*',
    //       '*',
    //       '*'
    //     ]);
    //     console.error(':', err);
    //   }
    // };

    const fetchDepartments = async () => {
        setDepartments([
          '*',
          '*',
          '*',
          '*'
        ]);
    };
    
    const debounceTimer = setTimeout(fetchDepartments, 300);
    return () => clearTimeout(debounceTimer);
  }, []);

  const handleSearch = async () => {
    if (!query || query.length > 200) {
      setError('200');
      return;
    }

    try {
      setIsLoading(true);
      const response = await axios.post('/api/mxb', {
        query,
        ksmc: selectedDepartment[0]?.label || ''
      });
      setResult(response.data.join('\n'));
      setError('');
    } catch (err) {
      setError('');
      console.error('API:', err);
    } finally {
      setIsLoading(false);
    }
  };

  const handleReset = () => {
    setQuery('');
    setResult('');
    setError('');
    setSelectedDepartment([]);
  };

  return (
    <div className="p-4">
      <Form className="mb-4">
        <div className="mb-3">
          <Typeahead
            id="department-select"
            options={departments}
            placeholder=""
            filterBy={(option, props) => option.toLowerCase().includes(props.text.toLowerCase())}
            minLength={1}
            onChange={(selected) => setSelectedDepartment(selected)}
            selected={selectedDepartment}
            labelKey="label"
          />
        </div>

        <FormControl
          as="textarea"
          rows={3}
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          placeholder="200"
          maxLength={200}
          className="mb-3"
        />

        {error && <Alert variant="danger">{error}</Alert>}

        <div className="d-grid gap-2 d-md-block">
          <Button
            variant="primary"
            onClick={handleSearch}
            disabled={isLoading}
            className="me-2"
          >
            {isLoading ? '...' : ''}
          </Button>
          <Button variant="secondary" onClick={handleReset}>
            
          </Button>
        </div>
      </Form>

      {result && (
        <FormControl
          as="textarea"
          rows={8}
          value={result}
          readOnly
          className="mt-3"
        />
      )}
    </div>
  );
};

export default SearchForm;